@import './fonts/webfontkit-20190914-061210/stylesheet.css';

.App {
  text-align: center;
  position: absolute;
  height: 100%;
  width: 100%;
  color:#f9f9f9;
}

.ascii {
  font-size: 15px;
}

.welcomeText {
  font-family: 'ms_sans_serifregular';
  font-size: 25px;
}

.mainContents {
  width: 100%;
  flex-direction: row;
  padding-top: 30px;
}

.tree {
  width: 30px;
  text-align: left;
  margin: auto;
  padding-right: 70px;
  border: solid black 0.5px;
  width: 200px;
  position: relative;
}

.treeOverlay {
  border: solid red 0.5px;
  width: 100%;
  height: 100%;
  position: absolute;
  display: flex;
  flex-direction: row;
  top: 0;
  left: 0;
}

.treeFirstLevel {
  border: solid green 0.5px;
  width: 69px;
}

.treeSecondLevel {
  border: solid purple 0.5px;
  width: 32px;
}

